import React, { useState } from "react";
import ReactDOM from "react-dom";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import { ThemeProvider } from "@material-ui/core";
import { createMuiTheme } from "@material-ui/core/styles";

let green = "#00bf03";
let red = "#f70000";
let yellow = "#a2ad00";

const theme = createMuiTheme({
  overrides: {
    MuiTableCell: {
      root: {
        //This can be referred from Material UI API documentation.
        padding: "4px 8px"
        // size: "small"
      }
    }
  }
});

export function tradingRangeLowfn(tableData, title, color, letr, direction) {
  if (tableData !== undefined) {
    return (
      <div className="TableC">
        <div className="TableTitle">
          <p>Trading Range </p>
          <p> - </p>
          <p style={{ color: color }}>{title}</p>
          <div className="Letr">
            <p> &#60;= </p>
            <p> {letr}% </p>
            <p> LETR </p>
          </div>
        </div>
        <ThemeProvider theme={theme}>
          <TableContainer>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell align="center"></TableCell>
                  <TableCell align="center"></TableCell>
                  <TableCell align="center" style={{ color: yellow }}>
                    Last
                  </TableCell>
                  <TableCell align="center" style={{ color: yellow }}>
                    Low
                  </TableCell>
                  <TableCell align="center">High</TableCell>
                  <TableCell align="center" style={{ color: color }}>
                    %{direction}
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {tableData.map((elem, index) => {
                  return (
                    <TableRow key={index}>
                      <TableCell align="center">{elem.fundName}</TableCell>
                      <TableCell align="center">{elem.ticker}</TableCell>
                      <TableCell align="center" style={{ color: yellow }}>
                        {elem.last}
                      </TableCell>
                      <TableCell align="center" style={{ color: yellow }}>
                        {elem.low}
                      </TableCell>
                      <TableCell align="center">{elem.high}</TableCell>
                      <TableCell align="center" style={{ color: color }}>
                        {elem.upside}
                      </TableCell>
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
          </TableContainer>
        </ThemeProvider>
      </div>
    );
  }
}

