export function momentumOpporfn(tableData) {
  if (tableData !== undefined) {
    return (
      <div className="TableC">
        <div className="TableTitle">
          <p>Momentum Indicator </p>
        </div>
        <ThemeProvider theme={theme}>
          <TableContainer>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell align="left"></TableCell>
                  <TableCell align="center">
                    <span style={{ color: red }}>Bearish</span> to Neutral{" "}
                  </TableCell>
                  <TableCell align="center">
                    Neutral to <span style={{ color: green }}>Bullish</span>
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {tableData.map((elem, index) => {
                  return (
                    <TableRow key={index}>
                      <TableCell align="left">{elem.fundName}</TableCell>
                      <TableCell align="center">{elem.bearNeutral}</TableCell>
                      <TableCell align="center">{elem.neutralBull}</TableCell>
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
          </TableContainer>
        </ThemeProvider>
        <p>Change relative to last trading day</p>
      </div>
    );
  }
}

